body {
  margin: 0;
  padding: 0;
  font-family: 'Josefin Sans', sans-serif;
  background-image: url(/showering/static/media/background.53fc6821.jpg);
  height: 100vh;
  width: 100vw;
  background-size: 100% 100%;
}

#root {
  width: 100%;
  height: 100%;
  display: flex;
}

.dropzone {
  margin: auto;
}

.cover {
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.8);
  display: flex;
  justify-content: center;
  align-items: center;
  opacity: 0;
  z-index: -1;
  flex-direction: column;
  transition: all 0.3s ease-in;
}

.cover.show {
  opacity: 1;
  z-index: 10;
}

.cover .text {
  font-size: 39px;
  color: white;
  margin-bottom: 40px;
  text-align: center;
}

.loading {}

li {
  display: flex;
  justify-content: space-between;
}

h1 {
  text-align: center;
}
li {
  border: 1px solid black;
  padding: 10px 20px;
  border-radius: 20px;
  margin-bottom: 20px;
}

.action span {
  margin-right: 10px;
}

.actions {
      display: flex;
    align-items: center;
}

.info {
  display: flex;
  flex-direction: column;
}
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}


ul {
  padding: 0;
