body {
  margin: 0;
  padding: 0;
  font-family: 'Josefin Sans', sans-serif;
  background-image: url(./background.jpg);
  height: 100vh;
  width: 100vw;
  background-size: 100% 100%;
}

#root {
  width: 100%;
  height: 100%;
  display: flex;
}

.dropzone {
  margin: auto;
}

.cover {
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.8);
  display: flex;
  justify-content: center;
  align-items: center;
  opacity: 0;
  z-index: -1;
  flex-direction: column;
  transition: all 0.3s ease-in;
}

.cover.show {
  opacity: 1;
  z-index: 10;
}

.cover .text {
  font-size: 39px;
  color: white;
  margin-bottom: 40px;
  text-align: center;
}

.loading {}

li {
  display: flex;
  justify-content: space-between;
}

h1 {
  text-align: center;
}